#splash {
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: black;
    .izquierda {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 50%;
      .iz-img {
        background-position: center;
        background-size: cover;
        width: 100%;
        height: 100%;
        opacity: 0.7; }
      #iz-img1 {
        background-image: url('../img/i-01.jpg'); }
      #iz-img2 {
        background-image: url('../img/i-02.jpg'); }
      #iz-img3 {
        background-image: url('../img/i-03.jpg'); } }
    .derecha {
      width: 50%;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      .de-img {
        background-position: center;
        background-size: cover;
        width: 100%;
        height: 100%;
        opacity: 0.7; }
      #de-img1 {
        background-image: url('../img/d-01.jpg'); }
      #de-img2 {
        background-image: url('../img/d-02.jpg'); }
      #de-img3 {
        background-image: url('../img/d-03.jpg'); } } }
  .word {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
      width: 350px; } } }


@media screen and (max-width: 750px) {
  #splash {
    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: black;
      .izquierda {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%; }
      .derecha {
        display: none; } }
    .word {
      top: 45%;
      img {
        width: 250px; } } } }
