#menu-header {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .iz {
    display: flex;
    .logo {
      img {
        width: 50px; } }
    .description {
      margin-left: 20px; } }
  .centro {
    padding-left: 50px;
    padding-right: 50px;
    img {
      width: 150px; } } }
@media screen and (max-width: 750px) {
  #menu-header {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    .iz {
      display: none; }
    .centro {
      img {
        width: 110px; } }
    .derecha {
      display: none; } } }
